    /**
     * 牌型分等级 用以区分特效 返回 1: 无牛 2: 牛1-牛9 3: 牛牛-五小牛
     * @param nType 牌型
     */
    public static sortBullPokerType(nType: number){
        if (nType == this.bullTypeDefine.BULL_NONE)
            return 1;
        if (nType >= this.bullTypeDefine.BULL_TEN && nType <= this.bullTypeDefine.BULL_SMALL)
            return 3;
        return 2;
    }

    // 检测是否要摆成3+2
    public static checkIsThreeWithTwo(ntype = 0){
        let nnFlag = ntype > this.bullTypeDefine.BULL_NONE && ntype != this.bullTypeDefine.BULL_BOMB && ntype != this.bullTypeDefine.BULL_SMALL;
        let zjhFlag = true;     // 金花固定3+2
        return nnFlag && zjhFlag;
    }

